.home-container {
    background: url('/official-site/images/skyDarkBlue.jpg') center center/cover no-repeat;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
}

.home-container > h1 {
    color: #fff;
    font-size: 100px;
    margin-top: -100px;
}

.home-container > p {
    margin-top: 8px;
    color: #fff;
    font-size: 2rem;
    font-family: 'Source Code Pro', monospace;
    font-weight: bold;
  }
  
  .welcome-container {
      margin-top: 32px;
  }

  .welcome-container .btn {
      margin: 6px;
      border-radius: 10px;
  }

  @media screen and (max-width: 1000px) {
      .home-container > h1 {
          font-size: 70px;
          margin-top: -150px;
      }      
  }

  @media screen and (max-width: 750px) {
      .home-container > h1 {
        font-size: 50px;
        margin-top: -100px;
      }

      .home-container > p {
          font-size: 30px;
      }

      .btn-home {
          display: block;
          text-decoration: none;
      }

      .btn {
          width: 100%;
      }
  }